import { initial } from './State';
import { update } from './Update';
import { view } from './View';
import { service } from './Service';
import { fetchTodos } from './Msg';
export const init = dispatch => {
    dispatch(fetchTodos());
    return initial;
};
export const app = {
    init, update, service, view
};



