import { initial as initialRequest } from './Request';
import { v4 as uuid } from 'uuid';
export const initial = {
    id: "",
    text: "",
    newText: "",
    editable: false,
    editRequest: initialRequest,
    removeRequest: initialRequest
};
export const from = ({ id, text }) => (Object.assign({}, initial, { id, text }));
export const create = (text) => (Object.assign({}, initial, { text, id: uuid() }));
export const idEquals = (id) => (state) => {
    return state.id === id;
};
export const idNotEquals = (id) => (state) => {
    return state.id !== id;
};
export const setEditable = (state) => (Object.assign({}, state, { editable: true, newText: state.text }));
export const setUneditable = (state) => (Object.assign({}, state, { editable: false, newText: "" }));
export const setNewText = (newText) => (state) => (Object.assign({}, state, { newText }));
export const setText = (text) => (state) => (Object.assign({}, state, { text }));
export const updateRemoveRequest = (...fns) => (state) => {
    return Object.assign({}, state, { removeRequest: fns.reduce((memo, fn) => fn(memo), state.removeRequest) });
};
export const updateEditRequest = (...fns) => (state) => {
    return Object.assign({}, state, { editRequest: fns.reduce((memo, fn) => fn(memo), state.removeRequest) });
};



