import jsx from 'inferno-create-element';
export const init = _ => 0;
export const update = (state, msg) => {
    switch (msg) {
        case "increment":
            return state + 1;
        case "decrement":
            return state - 1;
        case "reset":
            return 0;
        default:
            return state;
    }
};
export const service = _dispatch => _msg => { };
export const view = dispatch => state => {
    return (jsx("div", { id: "counter" },
        jsx("span", null, state),
        jsx("button", { onClick: _ => dispatch("decrement") }, "-"),
        jsx("button", { onClick: _ => dispatch("reset") }, "reset"),
        jsx("button", { onClick: _ => dispatch("increment") }, "+")));
};
export const app = { init, update, view, service };



