export const payload = (msg) => msg[1];
export const removeTodoFailed = (payload) => ["RemoveTodoFailed", payload];
export const isRemoveTodoFailed = (msg) => msg[0] === "RemoveTodoFailed";
export const fetchTodos = (payload = null) => ["FetchTodos", payload];
export const isFetchTodos = (msg) => msg[0] === "FetchTodos";
export const todosFetched = (payload) => ["TodosFetched", payload];
export const isTodosFetched = (msg) => msg[0] === "TodosFetched";
export const addTodoInput = (payload) => ["AddTodoInput", payload];
export const isAddTodoInput = (msg) => msg[0] === "AddTodoInput";
export const addTodo = (payload) => ["AddTodo", payload];
export const isAddTodo = (msg) => msg[0] === "AddTodo";
export const todoAdded = (payload) => ["TodoAdded", payload];
export const isTodoAdded = (msg) => msg[0] === "TodoAdded";
export const removeTodo = (payload) => ["RemoveTodo", payload];
export const isRemoveTodo = (msg) => msg[0] === "RemoveTodo";
export const todoRemoved = (payload) => ["TodoRemoved", payload];
export const isTodoRemoved = (msg) => msg[0] === "TodoRemoved";
export const beginEditTodo = (payload) => ["BeginEditTodo", payload];
export const isBeginEditTodo = (msg) => msg[0] === "BeginEditTodo";
export const editTodoInput = (payload) => ["EditTodoInput", payload];
export const isEditTodoInput = (msg) => msg[0] === "EditTodoInput";
export const cancelEditTodo = (payload) => ["CancelEditTodo", payload];
export const isCancelEditTodo = (msg) => msg[0] === "CancelEditTodo";
export const finishEditTodo = (payload) => ["FinishEditTodo", payload];
export const isFinishEditTodo = (msg) => msg[0] === "FinishEditTodo";
export const todoEdited = (payload) => ["TodoEdited", payload];
export const isTodoEdited = (msg) => msg[0] === "TodoEdited";



