export const initial = {
    active: false,
    error: null
};
export const setActive = (state) => (Object.assign({}, state, { active: true }));
export const setInactive = (state) => (Object.assign({}, state, { active: false }));
export const setError = (error) => (state) => (Object.assign({}, state, { error, active: false }));
export const clearError = (state) => (Object.assign({}, state, { error: null }));



