import * as Todos from './State/Todos';
import * as Request from './State/Request';
import * as AddTodoForm from './State/AddTodoForm';
export const initial = {
    todos: Todos.initial,
    fetchTodosRequest: Request.initial,
    addTodoForm: AddTodoForm.initial
};
export const set = (key) => (val) => (state) => {
    return Object.assign({}, state, { [key]: val });
};
export const update = (key, ...fns) => (state) => {
    return Object.assign({}, state, { [key]: fns.reduce((memo, fn) => fn(memo), state[key]) });
};
export const apply = (...fns) => (state) => {
    return fns.reduce((memo, fn) => fn(memo), state);
};



