import { apply, update as change } from './State';
import * as Request from './State/Request';
import * as Todos from './State/Todos';
import * as Todo from './State/Todo';
import * as AddTodoForm from './State/AddTodoForm';
import * as Msg from './Msg';
import { payload } from './Msg';
// Fetching todos
export const onFetchTodos = (_msg) => apply(change("fetchTodosRequest", Request.setActive));
export const onTodosFetched = (msg) => apply(change("todos", Todos.add(...payload(msg))), change("fetchTodosRequest", Request.setInactive));
// Adding a todo
export const onAddTodoInput = (msg) => apply(change("addTodoForm", AddTodoForm.setText(payload(msg))));
export const onAddTodo = (_msg) => apply(change("addTodoForm", AddTodoForm.updateRequest(Request.setActive)));
export const onTodoAdded = (msg) => apply(change("todos", Todos.add(payload(msg))), change("addTodoForm", AddTodoForm.clear, AddTodoForm.updateRequest(Request.setInactive)));
// Removing a todo
export const onRemoveTodo = (msg) => apply(change("todos", Todos.update(payload(msg), Todo.updateRemoveRequest(Request.setActive))));
export const onTodoRemoved = (msg) => apply(change("todos", Todos.remove(payload(msg))));
export const onRemoveTodoFailed = (msg) => apply(change("todos", Todos.update(payload(msg).id, Todo.updateRemoveRequest(Request.setError(payload(msg).error)))));
// Editing a todo
export const onBeginEditTodo = (msg) => apply(change("todos", Todos.update(payload(msg).id, Todo.setEditable)));
export const onEditTodoInput = (msg) => apply(change("todos", Todos.update(payload(msg).id, Todo.setNewText(payload(msg).newText))));
export const onCancelEditTodo = (msg) => apply(change("todos", Todos.update(payload(msg), Todo.setUneditable)));
export const onFinishEditTodo = (msg) => apply(change("todos", Todos.update(payload(msg).id, Todo.updateEditRequest(Request.setActive))));
export const onTodoEdited = (msg) => apply(change("todos", Todos.update(payload(msg).id, Todo.updateEditRequest(Request.setInactive), Todo.setUneditable, Todo.setText(payload(msg).text))));
export const update = (state, msg) => {
    if (Msg.isFetchTodos(msg))
        return onFetchTodos(msg)(state);
    if (Msg.isTodosFetched(msg))
        return onTodosFetched(msg)(state);
    if (Msg.isAddTodoInput(msg))
        return onAddTodoInput(msg)(state);
    if (Msg.isAddTodo(msg))
        return onAddTodo(msg)(state);
    if (Msg.isTodoAdded(msg))
        return onTodoAdded(msg)(state);
    if (Msg.isRemoveTodo(msg))
        return onRemoveTodo(msg)(state);
    if (Msg.isTodoRemoved(msg))
        return onTodoRemoved(msg)(state);
    if (Msg.isBeginEditTodo(msg))
        return onBeginEditTodo(msg)(state);
    if (Msg.isEditTodoInput(msg))
        return onEditTodoInput(msg)(state);
    if (Msg.isCancelEditTodo(msg))
        return onCancelEditTodo(msg)(state);
    if (Msg.isFinishEditTodo(msg))
        return onFinishEditTodo(msg)(state);
    if (Msg.isTodoEdited(msg))
        return onTodoEdited(msg)(state);
    if (Msg.isRemoveTodoFailed(msg))
        return onRemoveTodoFailed(msg)(state);
    return state;
};



