import { jsx } from '../Tea';
import { removeTodo, beginEditTodo, editTodoInput, cancelEditTodo, finishEditTodo } from '../Msg';
const KEYCODE_ENTER = 13;
const KEYCODE_ESC = 27;
export const listItem = (dispatch) => ({ id, text, newText, editable, editRequest, removeRequest }) => {
    const listItemId = `TodoListItem-${id}`;
    const inputSelector = `#${listItemId} > input`;
    const dispatchRemove = (_evt) => {
        dispatch(removeTodo(id));
    };
    const dispatchEdit = (_evt) => {
        if (!removeRequest.active) {
            dispatch(beginEditTodo({ id, inputSelector }));
        }
    };
    const dispatchCancelEdit = (_evt) => {
        dispatch(cancelEditTodo(id));
    };
    const handleInputKeyUp = (evt) => {
        if (evt.keyCode === KEYCODE_ESC) {
            dispatch(cancelEditTodo(id));
        }
        if (evt.keyCode === KEYCODE_ENTER) {
            dispatch(finishEditTodo({ id, newText }));
        }
        else {
            dispatch(editTodoInput({ id, newText: evt.target.value }));
        }
    };
    if (editable) {
        return (jsx("li", { id: listItemId },
            jsx("input", { type: "text", value: newText, disabled: editRequest.active, onKeyUp: handleInputKeyUp, onBlur: dispatchCancelEdit })));
    }
    else {
        return (jsx("li", { id: listItemId },
            jsx("span", { onClick: dispatchEdit }, text),
            jsx("button", { disabled: removeRequest.active, onClick: dispatchRemove }, "Remove"),
            removeErrorMessage(removeRequest)));
    }
};
const removeErrorMessage = (request) => {
    if (request.error !== null) {
        return jsx("div", null, request.error);
    }
    else {
        return jsx("span", null);
    }
};



