import { jsx } from '../Tea';
import { addTodo, addTodoInput } from '../Msg';
export const form = (dispatch) => ({ text, request }) => {
    const handleSubmit = (evt) => {
        evt.preventDefault();
        dispatch(addTodo(text));
    };
    const handleInput = (evt) => {
        dispatch(addTodoInput(evt.target.value));
    };
    return (jsx("form", { onSubmit: handleSubmit },
        jsx("input", { type: "text", value: text, disabled: request.active, onInput: handleInput, placeholder: "Add a todo..." })));
};



