import * as Todo from './State/Todo';
import { payload, isAddTodo, isRemoveTodo, isFetchTodos, isBeginEditTodo, isFinishEditTodo, todoAdded, todoRemoved, todosFetched, todoEdited } from './Msg';
const addTodo = (dispatch, msg) => setTimeout(dispatch, 250, todoAdded(Todo.create(payload(msg))));
const removeTodo = (dispatch, msg) => setTimeout(dispatch, 250, todoRemoved(payload(msg)));
const fetchTodos = (dispatch, _msg) => setTimeout(dispatch, 500, todosFetched([
    Todo.create('Do a thing'),
    Todo.create('Do another thing'),
]));
const beginEditTodo = (_dispatch, msg) => {
    const { inputSelector } = payload(msg);
    setImmediate(() => {
        const input = document.querySelector(inputSelector);
        input.focus();
        input.select();
    });
};
const finishEditTodo = (dispatch, msg) => {
    const { id, newText } = payload(msg);
    setTimeout(dispatch, 250, todoEdited({ id, text: newText }));
};
export const service = dispatch => (msg) => {
    if (isFetchTodos(msg))
        return fetchTodos(dispatch, msg);
    if (isRemoveTodo(msg))
        return removeTodo(dispatch, msg);
    if (isAddTodo(msg))
        return addTodo(dispatch, msg);
    if (isBeginEditTodo(msg))
        return beginEditTodo(dispatch, msg);
    if (isFinishEditTodo(msg))
        return finishEditTodo(dispatch, msg);
    return;
};



