import jsx from 'inferno-create-element';
const init = dispatch => {
    dispatch(["Focus", null]);
    return "World";
};
const view = dispatch => state => (jsx("div", null,
    jsx("p", null,
        "Hello ",
        state,
        "!"),
    jsx("input", { id: "HelloInput", type: "text", value: state, onInput: evt => dispatch(["Input", evt.target.value]) })));
const update = (state, msg) => {
    if (msg[0] === "Input") {
        return msg[1];
    }
    else {
        return state;
    }
};
const service = dispatch => msg => {
    if (msg[0] === "Focus") {
        const el = document.getElementById("HelloInput");
        el.focus();
        el.select();
    }
};
export const app = { init, view, update, service };



