import { initial as initialRequest } from './Request';
export const initial = {
    text: "",
    request: initialRequest
};
export const setText = (text) => (form) => (Object.assign({}, form, { text }));
export const clear = (form) => (Object.assign({}, form, { text: "" }));
export const updateRequest = (...fns) => (form) => {
    return Object.assign({}, form, { request: fns.reduce((memo, fn) => fn(memo), form.request) });
};



